@import "css/_mixins.scss";
@import "css/_theme-vars.scss";


/* 1. Reset tags
-------------------------------------------------------------- */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,input,textarea,button,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, address, section, video, audio {
	font-family: inherit;
	@include font(100%, inherit, inherit, inherit);
	border: 0;
	outline: 0;
	-ms-word-wrap: break-word;
	    word-wrap: break-word;
}
html, body {
	margin: 0;
	padding: 0;
	width: 100%;
}
html {
	font-size: 100%;	/*62.5%;*/		/* Corrects text resizing oddly in IE6/7 when body font-size is set using em units
										   http://clagnut.com/blog/348/#c790 */
	-webkit-text-size-adjust: 100%; 	/* Prevents iOS text size adjust after orientation change, without disabling user zoom */
	    -ms-text-size-adjust: 100%; 	/* www.456bereastreet.com/archive/201012/
										   controlling_text_size_in_safari_for_ios_without_disabling_user_zoom/ */
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	
	overflow-y: scroll !important;
	overflow-x: hidden;
	height: 100%;
}

// jQuery UI widgets reset
div.ui-widget,
div.ui-widget .ui-widget,
div.ui-widget .ui-widget-header,
div.ui-widget .ui-widget-content,
div.ui-accordion .ui-accordion-header,
div.ui-widget button,
div.ui-widget input,
div.ui-widget select,
div.ui-widget textarea,
div.ui-state-default,
div.ui-widget-content .ui-state-default,
div.ui-widget-header .ui-state-default {
	font-family: inherit;
	@include font(1em, inherit);
}
div.ui-widget,
div.ui-widget .ui-widget,
div.ui-widget .ui-widget-header,
div.ui-widget .ui-widget-content,
div.ui-accordion .ui-accordion-header,
div.ui-state-default,
div.ui-widget-content .ui-state-default,
div.ui-widget-header .ui-state-default {
	color: inherit;
	background: none;
	border-style: none;
	@include border-sharp;
}
div.ui-datepicker {
	background-color: #fff;
}
div.ui-accordion .ui-accordion-header {
	padding: 0;
}
div.ui-accordion .ui-accordion-header-icon {
	display: none;
}
div.ui-tabs,
div.ui-tabs .ui-tabs-panel,
div.ui-tabs .ui-tabs-nav {
	padding: 0;
}
div.ui-tabs .ui-tabs-nav li {
	float: none;
	margin: 0;
}
div.ui-tabs .ui-tabs-nav .ui-tabs-anchor {
	float:none;
}



/* 2. Default tag's settings
-------------------------------------------------------------- */
html {
	font-size: 14px;
}
body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	/* Next rule make text bolder in Mozilla on MacOS */
	/*text-rendering: optimizeLegibility;*/
}

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section {
	display: block;
}

h1, h2, h3, h4, h5, h6 {
	hyphens: auto;
}

li > ol, li > ul, dl > dd {
	margin-bottom:0;
}
li > p {
	margin-bottom:0;
}
li > p+p {
	margin-top:0.5em;
}
ol, ul {
	padding-left:1.5em;
}


/* Links */
a {
	text-decoration:none;
	background:transparent;
}
a,
a:hover,
a:focus,
a:active {
	outline: 0;
}
a img { border: none; }

a,
button,
input[type="button"],
input[type="submit"] {
	@include transition-colors;
}

h1 > a, h2 > a, h3 > a, h4 > a, h5 > a, h6 > a {
	display:block;
}

:active, :focus { outline:0; }

a[href="javascript:void(0)"] {
	cursor: default;
}

/* Tables */
table {
	border-collapse: collapse;
}
table th+th, table th+td, table td+th, table td+td {
	border-left: 1px solid #ddd;
}
table > p {
	margin: 0 !important;
}
table td, table th {
	padding: 1.8em 1em;
	border: 1px solid;
}
table th {
	@include font(11px, '', 700, normal);
	text-transform:uppercase;
	text-align: center;
}
caption {
	font-weight: 700;
	text-align: center;
}
table tr td {
	border-top: 0;
}
table tr:not(:last-child) td {
	border-bottom: 0;
}


/* Blockquotes*/
blockquote {
	position:relative;
	text-align:left;
	padding: 0.35em 3.2em 0.4em 1.1em;
	margin: 0;
	border-left:2px solid;
}

blockquote:after,
q:before, q:after {	content: ""; }
blockquote, q {	quotes: "" ""; }

blockquote p {
	margin: 0;
	@include font(16px, 21px, 700);
	letter-spacing: -0.48px;
}
blockquote > a, blockquote > p > a,
blockquote > cite, blockquote > p > cite {
	display:block;
	@include font(17px, 23px, 400, italic);
	text-transform: none;
	margin-top: 0.75em;
	letter-spacing: -0.6px;
}

.blog_mode_post blockquote,
.blog_mode_page blockquote {
	margin: 1.5em 0 1.3em;
}
.blog_mode_post .comments_list blockquote,
.blog_mode_page .comments_list blockquote {
	margin: 1.5em 0;
}


/* Other tags */
dd {
	margin-left: 1.5em;
}
dt, b, strong {
	font-weight: bold;
}
dfn, em, i {
	font-style: italic;
}

pre, code, kbd, tt, var, samp {
	font-family: "Courier New", Courier, monospace;
	font-size: 1em;
	letter-spacing:0;
}
pre {
	overflow: auto;
	max-width: 100%;
}
code {
	overflow: auto;
	max-width: 100%;
	padding: 1em;
	border: 1px solid #888;
	background-color: #f7f7f7;	
	display: inline-block;
	vertical-align: middle;
	word-wrap:break-word;
}
pre > code {
	display: block;
	vertical-align: top;
	padding: 1em;
}
abbr, acronym {
	border-bottom: 1px dotted;
	cursor: help;
}
mark, ins {
	background-color:transparent;
	text-decoration: none;
}
sup,
sub {
	font-size: 75%;
	height: 0;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}
sup { bottom: 1ex; }
sub { top: .5ex; }
small {	font-size: 80%; }
big {	font-size: 120%; }

[hidden], template {
	display: none;
}

hr {
	height: 0;
	border: none;
	border-top: 1px solid #eee;
	margin: 6.7857em 0;
	@include content-box;
}

/* Images */
img {
	max-width: 100%;
/*	width: auto; */	/* Only height: auto; not both! */
	height: auto;
	vertical-align:top;
}
figure,
.wp-caption,
.wp-caption-overlay .wp-caption {
	border: 0;
	margin: 0;
	padding: 0;
	overflow: hidden;
	position: relative;
	max-width:100%;
}
figure figcaption,
.wp-caption .wp-caption-text,
.wp-caption .wp-caption-dd,
.wp-caption-overlay .wp-caption .wp-caption-text,
.wp-caption-overlay .wp-caption .wp-caption-dd {
	@include abs-pos(auto, 0, 0, 0);
	@include font(11px, 15px, 700, normal);
	@include transition-property(margin-bottom);
	margin-bottom: 0;
	text-align:center;
	padding: 1.2em 10px 1.15em;
	text-transform: uppercase;
	border: 1px solid;
}

figure:hover figcaption,
.wp-caption:hover .wp-caption-text,
.wp-caption:hover .wp-caption-dd,
.wp-caption-overlay .wp-caption:hover .wp-caption-text,
.wp-caption-overlay .wp-caption:hover .wp-caption-dd {
	margin-bottom: -6em;
}
figure figcaption:hover,
.wp-caption .wp-caption-text:hover,
.wp-caption .wp-caption-dd:hover,
.wp-caption-overlay .wp-caption .wp-caption-text:hover,
.wp-caption-overlay .wp-caption .wp-caption-dd:hover {
	margin-bottom: 0;
}

svg:not(:root) { overflow: hidden; }



/* Audio and Video */
audio,
canvas,
progress,
video {
	display: inline-block;
	vertical-align: baseline;
}
audio:not([controls]) {
	display: none;
	height: 0;
}
iframe, video, embed {
	max-width: 100%;
	min-height: 100px;
	vertical-align: top;
}

/* Fontello icons */
[class^="icon-"]:before, [class*=" icon-"]:before {
	@include font(inherit, inherit, inherit, inherit);
	display: inline-block;
	vertical-align: top;
	width:auto;
	margin:0;
}




/* 3. Form fields settings
-------------------------------------------------------------- */

/* Common rules */
form {
	margin-bottom:0;
}
button, input, optgroup, select, textarea, textarea.wp-editor-area {
	font-family: inherit;
	font-size: 1em;				/* Corrects font size not being inherited in all browsers */
	margin: 0;					/* Addresses margins set differently in IE6/7, F3/4, S5, Chrome */
	vertical-align: baseline;	/* Improves appearance and consistency in all browsers */
}
button {
	overflow: visible; 
}
input[placeholder]::-webkit-input-placeholder,
textarea[placeholder]::-webkit-input-placeholder 	{ text-overflow:ellipsis; }
input[placeholder]::-moz-placeholder,
textarea[placeholder]::-moz-placeholder 			{ text-overflow:ellipsis; }
input[placeholder]:-ms-input-placeholder,
textarea[placeholder]:-ms-input-placeholder 		{ text-overflow:ellipsis; }
input[placeholder]::placeholder,
textarea[placeholder]::placeholder 					{ text-overflow:ellipsis; }
input[placeholder],
textarea[placeholder] 								{ text-overflow:ellipsis; }

textarea, textarea.wp-editor-area,
select, option,
input[type="text"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="tel"],
input[type="search"],
input[type="password"],
input[type="checkbox"],
input[type="radio"] {
	@include transition-colors;
	@include border-box;
	@include border-sharp;
}
input[type="checkbox"],
input[type="radio"] {
	padding: 0;						/* Addresses excess padding in IE8/9 */
}
/*input[type="number"]::-webkit-inner-spin-button,*/
input[type="number"]::-webkit-outer-spin-button {
	height: 50%;
}
input[type="number"],
input[type="search"] {
	-webkit-appearance: none;		/* Addresses appearance set to searchfield in S5, Chrome */
	-webkit-appearance: textfield;
}
/*input[type="search"]::-webkit-search-cancel-button,*/
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;		/* Corrects inner padding displayed oddly in S5, Chrome on OSX */
}
button::-moz-focus-inner,
input::-moz-focus-inner {			/* Corrects inner padding and border displayed oddly in FF3/4 www.sitepen.com/blog/2008/05/14/the-devils-in-the-details-fixing-dojos-toolbar-buttons/ */
	border: 0;
	padding: 0;
}
button[disabled],
html input[disabled] {
	cursor: default;
}
button:focus,
input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
select:focus,
textarea:focus,
textarea.wp-editor-area:focus {
	outline: none;
}
input[type="text"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="tel"],
input[type="password"],
input[type="search"],
select,
textarea,
textarea.wp-editor-area {
	-webkit-appearance: none;
	outline: none;
	resize: none;
}

/* Required fields */
label.required:after {
	content:'*';
	display: none;
	vertical-align:text-top;
	font-size:80%;
	color:#da6f5b;
}

/* Field set */
fieldset {
	padding: 1em 1.5em;
	margin: 1.5em 0.2em;
	border: 1px solid #ddd;
	position:relative;
}
fieldset legend {
	@include abs-lt(1.5em, -0.6em);
	background-color:#fff;
	font-weight:400;
	font-style: normal;
	padding: 0 0.2em;
	letter-spacing:0;
}


