/* 5. Theme grid
-------------------------------------------------------------- */
.container, .container-fluid {
	margin-right: auto;
	margin-left: auto;
	padding-left: $grid_gap / 2;
	padding-right: $grid_gap / 2;
}
.row, .columns_wrap {
	margin-left: 0px;
	margin-right: -$grid_gap;
}
.row > [class*="column-"],
.columns_wrap > [class*="column-"] {
	
	/* Old way: floating columns */
	/* float: left;*/
	
	/* New way: inline blocks */
	/* Warning: remove spaces between column's tags:
		Right:															Wrong:
		<div class="columns_wrap"><div class="column-1_2">				<div class="columns_wrap">
																			<div class="column-1_2">
			...																	...
		</div><div class="column-1_2">										</div
																			<div class="column-1_2">
			...																	...
		</div></div>														</div>
																		</div>
	*/
	display:inline-block;
	vertical-align:top;
	
	position: relative;
	min-height: 1px;
	padding-left: 0px;			// $grid_gap / 2;
	padding-right: $grid_gap;	// $grid_gap / 2;
	@include border-box;
}
.row.columns_padding_left, .columns_wrap.columns_padding_left, .vc_row.columns_padding_left {
	margin-left: -$grid_gap;
	margin-right: 0;
}
.vc_row.columns_padding_left .vc_column_container > .vc_column-inner,
.row.columns_padding_left > [class*="column-"], .columns_wrap.columns_padding_left > [class*="column-"],
.row > [class*="column-"].columns_padding_left, .columns_wrap > [class*="column-"].columns_padding_left {
	padding-left:$grid_gap;
	padding-right:0;
}
.vc_row.columns_padding_right,
.row.columns_padding_right,
.columns_wrap.columns_padding_right {
	margin-left: 0;
	margin-right: -$grid_gap;
}
.vc_row.columns_padding_right .vc_column_container > .vc_column-inner,
.row.columns_padding_right > [class*="column-"], .columns_wrap.columns_padding_right > [class*="column-"],
.row > [class*="column-"].columns_padding_right, .columns_wrap > [class*="column-"].columns_padding_right {
	padding-left:0;
	padding-right:$grid_gap;
}
.vc_row,
.row.columns_padding_center,
.columns_wrap.columns_padding_center {
	margin-left: -$grid_gap / 2;
	margin-right: -$grid_gap / 2;
}
.vc_column_container > .vc_column-inner,
.row.columns_padding_center > [class*="column-"], .columns_wrap.columns_padding_center > [class*="column-"],
.row > [class*="column-"].columns_padding_center, .columns_wrap > [class*="column-"].columns_padding_center {
	padding-left:$grid_gap / 2;
	padding-right:$grid_gap / 2;
}
.row.columns_padding_bottom > [class*="column-"], .columns_wrap.columns_padding_bottom > [class*="column-"],
.row > [class*="column-"].columns_padding_bottom, .columns_wrap > [class*="column-"].columns_padding_bottom {
	padding-bottom: $grid_gap;
}
.sc_blogger.no_margin .row, .sc_blogger.no_margin .columns_wrap,
.row.no_margin, .columns_wrap.no_margin {
	margin-left: 0 !important;
	margin-right: 0 !important;
}
.sc_blogger.no_margin .row > [class*="column-"], .sc_blogger.no_margin .columns_wrap > [class*="column-"],
.row.no_margin > [class*="column-"], .columns_wrap.no_margin > [class*="column-"] {
	padding: 0 !important;
}

/* Columns, push, pull and offset sizes */
@for $i from 1 through 12 {
	@for $j from $i through 12 {
		$s: 100% / $j * $i;
		@if $j == 1 {
			.column-#{$i},
			.column-#{$i}_#{$j} { width: $s; }
		} @else {
			.column-#{$i}_#{$j} { width: $s; }
			@if ($i < $j) {
			.push-#{$i}_#{$j} { left: $s }
			.pull-#{$i}_#{$j} { right: $s; }
			.offset-#{$i}_#{$j} { margin-left: $s; }
			}
		}
	}
}

/* Utils */
/*
.clearfix:before,
.container:before,
.container-fluid:before,
.row:before,
.columns_wrap:before {
  content: " ";
