/* 6. Page layouts
-------------------------------------------------------------- */
.page_wrap {
	min-height:100vh;
	overflow:hidden;	// To prevent the horizontal scroll bar appears
}
.page_wrap,
.content_wrap {
	margin:0 auto;
}
.content-title {
	border: 1px solid;
	display: block;
	padding: 1.08rem 2.23rem;
	font-size: 1.059em;
	font-weight: 500;
	line-height: 1.63;
	letter-spacing: 0.5px;
	background-image: url("images/top-header.jpg");
	background-size: cover;
}
.content-body {
	border: 1px solid;
	display: block;
	padding: 2.2rem 2.23rem;
	overflow: auto;
}
.woocommerce .content-body {
	overflow: inherit;
}
.content-title + .content-body {
	border-top: 0;
}
.body_style_boxed {
	background-color:#f0f0f0;
	background-attachment: fixed;
	@include bg-cover(center top);
}
.body_style_boxed .page_wrap {
	width: $page_boxed;
}
.page_content_wrap {
	/* padding-top: 15.35rem; */
	padding-bottom: 3.25rem;
	background-repeat: no-repeat;
}
.remove_margins .page_content_wrap {
	padding-top: 0 !important;
	padding-bottom: 0 !important;
}
.content_wrap,
.content_container {
	width: $page;
	margin:0 auto;
}

.content_wrap .content_wrap,
.content_wrap .content_container,
.content_container .content_wrap,
.content_container .content_container {
	width:100%;
}
.content_wrap:after,
.content_container:after {
	content:" ";
	@include clear;
}
.body_style_fullwide .content_wrap {
	max-width: $page_fullwide;		// To prevent stretching content on the extra wide screen
	margin: 0 auto;
	width: 100% !important;
	padding-left: $paddings_fullwide;
	padding-right: $paddings_fullwide;
	@include border-box;
}
.content, .sidebar, .sidebar_inner {
	@include border-box;
}
.page_content_wrap .content_wrap {
	position:relative;
}

/* Content and Sidebar */
body.body_style_wide:not(.expand_content) [class*="content_wrap"] > .content,
body.body_style_boxed:not(.expand_content) [class*="content_wrap"] > .content {	width: $content; }
[class*="content_wrap"] > .sidebar { 											width: $sidebar; }
.sidebar_hide [class*="content_wrap"] > .content { margin-left: auto; margin-right: auto; }
.sidebar_right [class*="content_wrap"] > .content { float: left; }
.sidebar_right [class*="content_wrap"] > .sidebar { float: right; }
.sidebar_left [class*="content_wrap"] > .content  { float: right; }
.sidebar_left [class*="content_wrap"] > .sidebar  { float: left; }

/* Fullwide or Fullscreen with sidebar */
.body_style_fullwide [class*="content_wrap"] > .content,
.body_style_fullscreen [class*="content_wrap"] > .content { width: 100%; /*width:auto; max-width:100%;*/ }
.body_style_fullwide.sidebar_right [class*="content_wrap"] > .content,
.body_style_fullscreen.sidebar_right [class*="content_wrap"] > .content { padding-right: $gap + $sidebar; }
.body_style_fullwide.sidebar_right [class*="content_wrap"] > .sidebar,
.body_style_fullscreen.sidebar_right [class*="content_wrap"] > .sidebar { margin-left: -$sidebar; }
.body_style_fullwide.sidebar_left [class*="content_wrap"] > .content,
.body_style_fullscreen.sidebar_left [class*="content_wrap"] > .content { padding-left:  $gap + $sidebar; }
.body_style_fullwide.sidebar_left [class*="content_wrap"] > .sidebar,
.body_style_fullscreen.sidebar_left [class*="content_wrap"] > .sidebar { margin-right:-$sidebar; }

.body_style_fullscreen .page_content_wrap {
	overflow:hidden;
	padding:0;
	position:relative;	/* Need to correct 'sticky' sidebar position */
}

/* Boxed style for articles and sidebar */
.body_style_fullscreen [class*="content_wrap"] > .content > article.page {
	padding:0;
}



