/* 7.6 Blog layouts
-------------------------------------------------------------- */

/* Excerpt */
.post_layout_excerpt {
	position:relative;
	margin-bottom:0;
}
.post_layout_excerpt + .post_layout_excerpt {
	margin-top: 3.4rem;
	padding-top: 3.3rem;
	border-top: 1px solid #eee;
}
.post_layout_excerpt.sticky {
	padding: 0;
	border:0;
}

.post_layout_excerpt .post_featured {
	display: inline-block;
}
/*.post_layout_excerpt.post_format_audio .post_featured,*/
