body[class*="blog_style_classic"] .post_layout_excerpt,
body[class*="blog_style_masonry"] .post_layout_excerpt {
	margin-bottom: 3em;
}


/* Chess */
.chess_wrap {
	overflow:hidden;
}
.post_layout_chess {
	position:relative;
	overflow:hidden;
	margin-bottom:0;
}
.post_layout_chess.sticky {
	padding:0;
	border-width: 0;
}
.post_layout_chess .label_sticky {
	top: -12px;
	left: -12px;
	right: auto;
	@include rotate(135deg);
}
.post_layout_chess .post_featured {
	margin-bottom:0;
	width: 50%;
	float: right;
	max-height:none !important;
}
.post_layout_chess .post_featured_bg:before {
	padding-top: 100%;	/* 112.5% - 16:9 */
}
.post_layout_chess .post_inner {
	width: 50%;
	@include abs-lc;
}
.post_layout_chess blockquote {
	margin:0;
}
.post_layout_chess_1 .post_inner_content {
	padding: 0 14%;
}
.post_layout_chess_2 .post_inner_content {
	padding: 0 12%;
}
.post_layout_chess_3 .post_inner_content {
	padding: 0 10%;
}
.post_layout_chess .post_header {
	padding-top:0.1px;
}
.post_layout_chess .post_title {
	margin: 0;
}
.post_layout_chess .post_meta {
	margin-top: 1em;
	margin-bottom: 0 !important;
}
.post_layout_chess .post_header + .post_content {
	margin-top:1em;
}
.post_layout_chess .post_content_inner {
	overflow:hidden;
	position:relative;
	padding-bottom: 1em;
}
.post_layout_chess:not(.post_format_aside) .post_content_inner {
	padding-top: 0;
}
.post_layout_chess .post_content_inner:after {
	content:' ';
	@include box(100%, 1em);
	@include abs-lb;
}

.post_layout_chess_1.post_format_quote .post_content_inner .post_meta,
.post_layout_chess_1.post_format_link .post_content_inner .post_meta,
.post_layout_chess_1.post_format_status .post_content_inner .post_meta {
	margin-top: 1rem;
}
.post_layout_chess_1.post_format_aside .post_content_inner .post_meta {
	margin-top: 3rem;
}
.post_layout_chess_1 .post_header + .post_content {
	margin-top:3.8rem;
}
.post_layout_chess.post_layout_chess_1 .more-link {
	margin-top: 3rem;
}

.post_layout_chess_1.post_format_quote .post_header,
.post_layout_chess_1.post_format_link .post_header,
.post_layout_chess_1.post_format_aside .post_header,
.post_layout_chess_1.post_format_status .post_header,
.post_layout_chess_2.post_format_quote .post_header,
.post_layout_chess_2.post_format_link .post_header,
.post_layout_chess_2.post_format_aside .post_header,
.post_layout_chess_2.post_format_status .post_header {
	display:none;
}

.post_layout_chess_2 .post_title {
	font-size:1.6rem;
}
.post_layout_chess_2:not(.post_format_quote):not(.post_format_link):not(.post_format_aside):not(.post_format_status) .post_content_inner {
	max-height: 15.4em;
}
.post_layout_chess_2 .post_meta,
.post_layout_chess_3 .post_meta {
	margin-top: 8px;
}
.post_layout_chess_3 .post_meta {
	display: none;
}
.post_layout_chess_2.post_format_aside .post_meta {
	margin-top: 1.3333rem;
}
.post_layout_chess_2.post_format_audio .post_featured .post_audio_title,
.post_layout_chess_3.post_format_audio .post_featured .post_audio_title {
	letter-spacing:0;
}
.post_layout_chess_2.format-chat p > em, .post_layout_chess_2.format-chat p > b, .post_layout_chess_2.format-chat p > strong {
	margin-top:0.8em;
}
.post_layout_chess.post_layout_chess_2 .more-link,
.post_layout_chess.post_layout_chess_3 .more-link {
	margin-top: 0.5rem;
}

.post_layout_chess_3 .post_title {
	@include font(1.3333rem, 1.2em);
}
.post_layout_chess_3 .post_content_inner {
	@include font(0.8667rem, 1.3486em);
	max-height: 6.2rem;
}
.post_layout_chess_3 .post_content_inner,
.post_layout_chess_3 .post_content_inner + .post_meta {
	display:none;
}
.post_layout_chess.post_layout_chess_3 .more-link {
}

.post_layout_chess_1:nth-child(2n+2) .post_featured {	float:left; }
.post_layout_chess_1:nth-child(2n+2) .post_inner {		left: auto; right:0; }

.post_layout_chess_2 {									width: 50%; float:left; }
.post_layout_chess_2:nth-child(4n+3) .post_featured,
.post_layout_chess_2:nth-child(4n+4) .post_featured {	float:left; }
.post_layout_chess_2:nth-child(4n+3) .post_inner,
.post_layout_chess_2:nth-child(4n+4) .post_inner {		left: auto; right:0; }

.post_layout_chess_3 {									width: 33.3333%; float:left; }
.post_layout_chess_3:nth-child(6n+4) .post_featured,
.post_layout_chess_3:nth-child(6n+5) .post_featured,
.post_layout_chess_3:nth-child(6n+6) .post_featured {	float:left; }
.post_layout_chess_3:nth-child(6n+4) .post_inner,
.post_layout_chess_3:nth-child(6n+5) .post_inner,
.post_layout_chess_3:nth-child(6n+6) .post_inner {		left: auto; right:0; }


/* Portfolio */
.post_layout_portfolio .post_title {
	margin: 1em 0 0.4em;
}
.post_layout_portfolio .post_featured {
	margin-bottom:0;
}
.post_layout_portfolio .post_featured img {
	width: 100%;
}


/*  This way use masonry.js script to build masonry layout and work properly in any browsers!
---------------------------------------------------------------------------------------------------------- */
.masonry_wrap,
.portfolio_wrap {
	position:relative;
	margin-right:-$grid_gap;
}
.masonry_wrap .masonry_item,
.post_layout_portfolio {
	position:relative;
    display: inline-block;
	vertical-align:top;
	padding:0 $grid_gap $grid_gap 0;
	margin-bottom:0;
    width: 50%;
	border-width: 0;
	@include border-box;
}
.masonry_wrap .masonry_item-1_3,
.portfolio_wrap.portfolio_3 .post_layout_portfolio {
    width: 33.3333%;
}
.masonry_wrap .masonry_item-1_4,
.portfolio_wrap.portfolio_4 .post_layout_portfolio {
    width: 25%;
}
.masonry_wrap .masonry_item {
	padding-bottom: 0;
}
.masonry_wrap .post_layout_masonry {
	margin-bottom: 30px;
}
.post_layout_portfolio .label_sticky {
	top: -12px;
	left: -12px;
	right:auto;
	@include rotate(135deg);
}

/* Gallery */
.post_layout_gallery * {
	@include border-box;
}
.post_layout_gallery .post_details {
	display:none;
}
.gallery_item_current {
	opacity: 0 !important;
}
.gallery_preview {
	@include fixed-lt(0, 0, 200000);
	@include box(50%, 100%);
	@include flex;
	@include flex-align-items(center);
	@include flex-align-content(center);
	@include flex-justify-content(center);
	pointer-events: none;
}
.gallery_preview:before {
	content: ' ';
	opacity: 0;
	@include fixed-cover;
	@include transition-property(opacity, 0.6s);
}
.gallery_preview_show {
	pointer-events: auto;
}
.gallery_preview_show:before {
	opacity: 1;
}

.gallery_preview_image_clone {
	position: fixed;
	z-index: 110;
	@include transition-property(transform, 0.5s);
	-webkit-backface-visibility: hidden;
}
.gallery_preview_image_original {
	position: relative;
	z-index: 120;
	display: block;
	object-fit: contain;
	@include transition-property(opacity, 0.2s);
	-webkit-backface-visibility: hidden;
}

.gallery_preview_show .gallery_preview_image_animate {				/* open */
	@include transition(transform 0.6s, opacity 0.2s);
}
.gallery_preview_image_animate {									/* close */
	@include transition(transform 0.3s, opacity 0.2s);
}

.gallery_preview_description {
	@include abs-lt(100%, 0, 140);
	@include box(100%, 100%);
	@include border-box;
	@include flex;
	@include flex-direction(column);
	@include flex-align-items(flex-start);
	@include flex-justify-content(center);
	@include transition(transform 1s, opacity 1s);
	@include transition-timing(0.2, 1, 0.3, 1);
	@include translate3d(0, 30px, 0);
	padding: 0 3em;
	opacity: 0;
}

.gallery_preview_show .gallery_preview_description {
	opacity: 1;
	@include transition-delay(0.2s);
	@include translate3d(0, 0, 0);
}

.gallery_preview_show .post_title {
	margin: 0 0 0.5em;
}
.gallery_preview_show .post_meta {
	@include font(1em, 1.3em);
	margin-top:0;
	margin-bottom:2em;
}
.gallery_preview_show .post_description_content {
	max-width: 100%;
	@include font(1.1429em, 2em);
}

.gallery_preview_close {
	font-size: 2em;
	margin: 0;
	padding: 0;
	cursor: pointer;
	vertical-align: top;
	border: none;
	background: none;
	padding: 1em;
	opacity: 0;
	@include fixed-rt(0, 0, 150);
	@include transition(transform 0.3s, opacity 0.3s);
	@include scale3d(0.6, 0.6, 1);
}
.gallery_preview_image_loaded .gallery_preview_close {
	opacity: 1;
	@include scale3d(1, 1, 1);
}



/* 7.7 Paginations
-------------------------------------------------------------- */

/* Blog pagination: Load more and Infinite */
.nav-links-more {
	text-transform:uppercase;
	letter-spacing:0px;
	text-align:center;
	margin-top: 3.2em;
	font-size: 15px;
	font-weight: 500;
	border-top: 1px solid;
	padding-top: 1.9em;
}
[class*="blog_style_classic"] .nav-links-more,
[class*="blog_style_masonry"] .nav-links-more {
	margin-top: 0;
}
.nav-links-more.nav-links-infinite {
	display:none;
}
.nav-links-more a {
	display:inline-block;
	position:relative;
}
.nav-links-more a:before {
	content: '';
	display: block !important;
	position: absolute;
	top: 50%;
	left: 50%;
	right: auto;
	bottom: auto;
	transform: translatex(-50%) translatey(-50%);
	width: 80%;
	height: 0;
	box-shadow: 0 0 70px 10px rgba(255, 0, 0, 0.7);
	opacity: 0;
	transition: all ease 0.3s;
}
.nav-links-more a:hover:before {
	opacity: 1;
}
.nav-links-infinite a span {
	opacity: 0;
}
.nav-links-infinite.loading {
	display: block;
}


/* Blog pagination: Prev/Next links */
.nav-links-old {
	overflow:hidden;
	font-size: 0.9333em;
	text-transform:uppercase;
	letter-spacing:1px;
	margin-top: 3em;
}
body[class*="blog_style_portfolio"] .nav-links-old,
body[class*="blog_style_classic"] .nav-links-old {
	margin-top:1.3em;
}
.nav-links-old .nav-prev a:before,
.nav-links-old .nav-next a:after {
	font-family:$theme_icons;
	font-weight:normal;
	display:inline;
	@include abs-lt;
}
.nav-links-old .nav-prev {
	float:left;
	position:relative;
	padding-left: 2em;
}
.nav-links-old .nav-prev a:before {
	content: '\e939';
}
.nav-links-old .nav-next {
	float:right;
	position:relative;
	padding-right:2em;
}
.nav-links-old .nav-next a:after {
	content: '\e93a';
	left: auto;
	right: 0;
}
.nav-links-old a {
	border-bottom: 1px dotted transparent;
}

/* Blog pagination: Page numbers */
div.esg-filters,
.woocommerce nav.woocommerce-pagination ul,
.comments_pagination,
.nav-links,
.page_links {
	text-transform:uppercase;
	clear: both;
	margin-top: 2.5em;
	padding-top:2.5em;
	border:none;
	border-top: 1px solid;
	text-align:center;
	list-style-type:none;
	width: 100%;
	font-size: 16px;
}
.page_links .page_links_title {
	display: inline-block;
	vertical-align: top;
	margin: 0.95em 1em 0 0;
}
.esg-filters div.esg-navigationbutton,
.woocommerce nav.woocommerce-pagination ul li a,
.woocommerce nav.woocommerce-pagination ul li span.current,
.page_links > span:not(.page_links_title),
.page_links > a,
.comments_pagination .page-numbers,
.nav-links .page-numbers {
	display:inline-block;
	vertical-align:top;
	font-size:inherit;
	font-weight: inherit;
	margin: 0 0.6667em 0 0;
	padding: 0;
	border: none;
	@include square(3em);
	@include border-sharp;
	@include transition-colors;
}
.esg-filters .esg-navigationbutton i[class*="eg-icon-"] {
	color: inherit;
}
.esg-filters .esg-navigationbutton i.eg-icon-left-open:before {
	content: '\e939';
	font-family: $theme_icons;
}
.esg-filters .esg-navigationbutton i.eg-icon-right-open:before {
	content: '\e93a';
	font-family: $theme_icons;
}
.woocommerce nav.woocommerce-pagination ul li {
	border: none;
	display:inline-block;
	margin:0 0.6667em 0 0;
}
.woocommerce nav.woocommerce-pagination ul li a,
.woocommerce nav.woocommerce-pagination ul li span.current {
	margin-right: 0;
}
.esg-filters div.esg-navigationbutton:hover,
.esg-filters div.esg-navigationbutton.selected {
	@include box-shadow(none);
}
.comments_pagination .page-numbers.prev,
.comments_pagination .page-numbers.next {
	width: auto;
	padding: 0 2em;
}
.nav-links .page-numbers.prev,
.nav-links .page-numbers.next,
.woocommerce nav.woocommerce-pagination ul li a.prev,
.woocommerce nav.woocommerce-pagination ul li a.next {
	text-indent: 100px;
	overflow:hidden;
	position:relative;
}
.nav-links .page-numbers.prev:before,
.nav-links .page-numbers.next:before,
.woocommerce nav.woocommerce-pagination ul li a.prev:before,
.woocommerce nav.woocommerce-pagination ul li a.next:before {
	font-family:$theme_icons;
	@include abs-lt;
	width: 100%;
	text-align: center;
	text-indent: 0;
}
.nav-links .page-numbers.prev:before,
.woocommerce nav.woocommerce-pagination ul li a.prev:before {
	content:'\e939';
}
.nav-links .page-numbers.next:before,
.woocommerce nav.woocommerce-pagination ul li a.next:before {
	content:'\e93a';
}


/* Single page Prev/Next navigation */
.nav-links-single .nav-links {
	font-size:0.8667em;
	text-align: left;
	margin-top: 6em;
	padding-top: 6em;
	border-top: 1px solid #ddd;
	overflow:hidden;
	text-transform:none;
}
.nav-links-single .nav-links a {
	display:block;
	position:relative;
	min-height: 5em;
	z-index:1;
}
.nav-links-single .nav-links a .nav-arrow {
	@include box(5em, 4.6em, 4.6em);
	overflow:hidden;
	text-align:center;
	@include abs-lt;
	@include bg-cover;
}
.nav-links-single .nav-links a .nav-arrow:before {
	content: ' ';
	display:block;
	background-color:#fff;
	opacity:0;
	@include abs-cover;
	@include transition-all;
}
.nav-links-single .nav-links a:hover .nav-arrow:before {
	opacity:0.5;
}
.nav-links-single .nav-links a .nav-arrow:after {
	content: '\e939';
	font-family: $theme_icons;
	display:inline-block;
	position:relative;
	z-index:2;
	top: 3em;
	opacity:0;
	color: #292929;
	@include transition-all;
}
.nav-links-single .nav-links a:hover .nav-arrow:after {
	top: 0;
	opacity:1;
}

.nav-links-single .nav-links .nav-next a .nav-arrow {
	right: 0;
	left: auto;
}
.nav-links-single .nav-links .nav-next a .nav-arrow:after {
	content: '\e93a';
}

.nav-links-single .nav-links .nav-previous,
.nav-links-single .nav-links .nav-next {
	width:50%;
	@include border-box;
}
.nav-links-single .nav-links .nav-previous {
	float:left;
	text-align:left;
	padding-right:15px;
}
.nav-links-single .nav-links .nav-previous a {
	padding-left: 6.5em;
}
.nav-links-single .nav-links .nav-previous .post-title {
	text-align:left;
}

.nav-links-single .nav-links .nav-next {
	float:right;
	text-align:right;
	padding-left:15px;
}
.nav-links-single .nav-links .nav-next a {
	padding-right: 6.5em;
}
.nav-links-single .nav-links .nav-next .post-title {
	text-align:right;
}

.nav-links-single .nav-links .post-title,
.nav-links-single .nav-links .post_date {
