/* Widget: Calendar */
.widget_calendar table {
	width: 100%;
	position:relative;
}
.widget_calendar caption {
	padding-bottom: 1em;
	font-weight: 700;
	font-size: 16px;
	border-bottom: 1px solid;
	margin-top: -5px;
	margin-bottom: 0.8rem;
}
.widget_calendar table > thead > tr {
	background-color: transparent !important;
}

.widget_calendar th,
.widget_calendar td {
	@include font(1em !important, 1.5em, 400 !important, normal);
	text-align:center;
	text-transform:uppercase;
	padding: 2px 0 6px;
	border: none !important;
	background-color: transparent !important;
	width: 	14.2857%;
	@include border-box;
}
.widget_calendar td {
	line-height:2.25em;
	padding:0;
}
.widget_calendar tbody td a {
	display:block;
	position:relative;
	font-weight:normal;
	@include border-box;
}
.widget_calendar tbody td a:after {
	content: ' ';
	@include abs-lb(48%, 6px);
	@include square(3px);
	@include transition-bg-color;
}
.widget_calendar td#today {
	position:relative;
	z-index:2;
}

.widget_calendar td#today:before {
	content:' ';
	@include abs-cc(-1);
	@include square(34px);
	border: 1px solid;
	margin-top: -2px;
}
.widget_calendar tfoot {
	position:relative;
	top: 0.6em;
}
.widget_calendar tfoot td {
	text-transform: none !important;
}
.widget_calendar td#prev {
	text-align:left;
	overflow:hidden;
}
.widget_calendar td#next {
	text-align:right;
}
.widget_calendar td#prev a,
.widget_calendar td#next a {
	position:relative;
}
.widget_calendar td#prev a {
	padding-left: 0.6em;
}
.widget_calendar td#next a {
	padding-right: 0.6em;
}
.widget_calendar td#prev a:before,
.widget_calendar td#next a:before {
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	width: 1.4em;
	height: 1.6em;
	line-height: 1.2em;
	display: inline-block;
	font-size: 14px;
	text-align: center;
}
.widget_calendar td#prev a:before {
	content: '\e958';
	font-family:$theme_icons;
}
.widget_calendar td#next a:before {
	content: '\e959';
	font-family:$theme_icons;
	left: auto;
	right: 0;
}

